.form-grid-container.enhanced-grid-container {
  border: 1px solid var(--table-border-color);
  border-radius: var(--border-radius-md);
  overflow-x: clip !important;
}
.form-grid-container.enhanced-grid-container .form-grid {
  border: none;
  border-radius: unset;
}
.form-grid-container.enhanced-grid-container .form-grid.relative-important {
  position: relative !important;
}
.form-grid-container.enhanced-grid-container .grid-heading-row {
  height: auto;
}
.form-grid-container.enhanced-grid-container .grid-heading-row .grid-row .static-area.ellipsis {
  text-overflow: unset;
  white-space: normal;
}
.form-grid-container.enhanced-grid-container .grid-heading-row .grid-static-col,
.form-grid-container.enhanced-grid-container .grid-heading-row .row-check,
.form-grid-container.enhanced-grid-container .grid-heading-row .row-index {
  height: auto !important;
  background-color: var(--subtle-fg);
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row {
  flex-wrap: nowrap;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col {
  max-width: 100px !important;
  min-width: 100px !important;
  border-bottom: 1px solid var(--table-border-color);
  padding-bottom: 1px;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col .field-area {
  border-bottom: 1px solid var(--table-border-color);
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col.custom-1 {
  min-width: 150px !important;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col.custom-2 {
  min-width: 200px !important;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col.custom-3 {
  min-width: 250px !important;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col.custom-4 {
  min-width: 300px !important;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col.custom-5 {
  min-width: 350px !important;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col.custom-6 {
  min-width: 400px !important;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col.custom-7 {
  min-width: 450px !important;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col.custom-8 {
  min-width: 500px !important;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col.custom-9 {
  min-width: 550px !important;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .col.custom-10 {
  min-width: 600px !important;
}
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .row-check,
.form-grid-container.enhanced-grid-container .grid-row .data-row.row .row-index {
  max-width: 50px !important;
  min-width: 50px !important;
}
.form-grid-container.enhanced-grid-container .slidecontainer {
  width: 100%;
}
.form-grid-container.enhanced-grid-container .enhanced-slider {
  -webkit-appearance: none;
  width: 100%;
  height: 8px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  transition: opacity 0.2s;
}
.form-grid-container.enhanced-grid-container .enhanced-slider:hover {
  opacity: 1;
}
.form-grid-container.enhanced-grid-container .enhanced-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 30%;
  height: 5px;
  background-color: grey;
  cursor: pointer;
}
.form-grid-container.enhanced-grid-container .enhanced-slider::-moz-range-thumb {
  width: 30%;
  height: 5px;
  background-color: grey;
  cursor: pointer;
}